header
  position: relative
  height: 200px
  background: linear-gradient(var(--Black) 50%, var(--White) 50%)
  box-shadow: 0 0 10px 2px #0000006e
  overflow: hidden

  .author
    position: relative
    z-index: 1
    width: 150px
    height: 150px
    margin: 0 auto
    overflow: hidden
    border-radius: 50%
    transform: translate(0px, 25px)

    img
      width: 300px
      transform: translate(-60px, -70px)
  
  .slider
    position: relative
    bottom: 35px
    gap: 50px
    display: flex

    .countries
      gap: 50px
      list-style-type: none
      animation: scroll 10s linear infinite
      display: flex

      .country
        img
          width: 70px

@keyframes scroll
  0%
    transform: translateX(0)
  100%
    transform: translateX(calc(-100% - 3rem))